import React, { Component } from "react";
import "./Skills.css";
// import THREE from "./threejs/three";
import * as THREE from "three";
import * as OrbitControls from "three-orbitcontrols";

class Skills extends Component {
  state = {
    selectedSkills: "Programming",
    programming: {
      top: {
        group: "Language",
        skills: [
          { name: "HTML", value: 95 },
          { name: "CSS", value: 95 },
          { name: "JavaScript", value: 80 },
          { name: "Python", value: 70 },
          { name: "SQL", value: 70 }
        ]
      },
      bottom: {
        group: "Library",
        skills: [
          { name: "React", value: 50 },
          { name: "Node.js", value: 50 },
          { name: "three.js", value: 70 },
          { name: "Potree", value: 80 },
          { name: "D3.js", value: 50 }
        ]
      }
    },
    engineering: {
      top: {
        group: "Industry",
        skills: [
          { name: "Geotech", value: 90 },
          { name: "Highways", value: 90 },
          { name: "Rail", value: 80 },
          { name: "Maritime", value: 70 },
          { name: "Buildings", value: 60 }
        ]
      },
      bottom: {
        group: "Software",
        skills: [
          { name: "ArcGIS", value: 90 },
          { name: "FME", value: 90 },
          { name: "AutoCAD", value: 70 },
          { name: "Office", value: 90 },
          { name: "Unity3D", value: 60 }
        ]
      }
    }
  };

  componentDidMount() {
    const width = this.mount.clientWidth;
    const height = this.mount.clientHeight;

    //ADD SCENE
    this.scene = new THREE.Scene();

    //ADD CAMERA
    this.camera = new THREE.PerspectiveCamera(50, width / height, 0.1, 1000);
    this.camera.position.z = 400;

    //ADD RENDERER
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setClearColor("#444444");
    this.renderer.setSize(width, height);
    this.mount.appendChild(this.renderer.domElement);

    //ADD LIGHT
    // var light = new THREE.AmbientLight(0x404040); // soft white light
    // this.scene.add(light);
    // var light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1);
    // this.scene.add(light);

    //ADD SCALES
    var scales = [25, 50, 75, 100];
    scales.map(this.renderSphere);

    //ADD AXIS LINES
    var skills;

    this.state.selectedSkills === "Engineering"
      ? (skills = this.state.engineering)
      : (skills = this.state.programming);

    this.axisValue = [];
    this.skillsValue = [];

    var i, label, value, radius, phi, theta;

    for (i = 0; i < skills.top.skills.length; i++) {
      label = skills.top.skills[i].name;
      value = skills.top.skills[i].value;
      radius = 150;
      phi = Math.PI / 4;
      theta = ((i + 1) * 2 * Math.PI) / 5;
      this.renderAxis(label, value, radius, phi, theta);
    }

    for (i = 0; i < skills.bottom.skills.length; i++) {
      label = skills.bottom.skills[i].name;
      value = skills.bottom.skills[i].value;
      radius = 150;
      phi = (3 * Math.PI) / 4;
      theta = ((i + 1.5) * 2 * Math.PI) / 5;
      this.renderAxis(label, value, radius, phi, theta);
    }

    // DRAW POLYGON

    var faceVerticesOrder = [
      // Top Face
      { v1: 0, v2: 1, v3: 2 },
      { v1: 0, v2: 2, v3: 3 },
      { v1: 0, v2: 3, v3: 4 },
      // Top Side
      { v1: 0, v2: 5, v3: 1 },
      { v1: 1, v2: 6, v3: 2 },
      { v1: 2, v2: 7, v3: 3 },
      { v1: 3, v2: 8, v3: 4 },
      { v1: 4, v2: 9, v3: 0 },
      // Bottom Face
      { v1: 7, v2: 6, v3: 5 },
      { v1: 8, v2: 7, v3: 5 },
      { v1: 9, v2: 8, v3: 5 },
      // Bottom Side
      { v1: 6, v2: 1, v3: 5 },
      { v1: 7, v2: 2, v3: 6 },
      { v1: 8, v2: 3, v3: 7 },
      { v1: 9, v2: 4, v3: 8 },
      { v1: 5, v2: 0, v3: 9 }
    ];

    faceVerticesOrder.map(this.renderBoundingWireframe);
    faceVerticesOrder.map(this.renderPolygon);

    //ADD TEXT
    var loader = new THREE.FontLoader();

    loader.load(
      process.env.PUBLIC_URL + "/fonts/helvetiker_regular.typeface.json",
      font => {
        var geometry = new THREE.TextGeometry("Skills & Experience", {
          font: font,
          size: 10,
          height: 0.1,
          curveSegments: 12
        });
        geometry.translate(105, 0, 0);
        var material = new THREE.MeshBasicMaterial({ color: "#00FFFF" });
        var label = new THREE.Mesh(geometry, material);
        this.scene.add(label);
      }
    );

    //ADD CONTROLS
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enableDamping = true;
    this.controls.dampingFactor = 0.2;
    this.controls.rotateSpeed = 0.2;
    this.controls.enableZoom = false;
    this.controls.enablePan = false;

    //Render and Animate Scene
    this.renderScene();
    this.animate();
  }

  componentWillUnmount() {
    this.mount.removeChild(this.renderer.domElement);
  }

  animate = () => {
    requestAnimationFrame(this.animate);

    // required if controls.enableDamping or controls.autoRotate are set to true
    this.controls.update();

    this.renderer.render(this.scene, this.camera);
  };

  renderScene = () => {
    this.renderer.render(this.scene, this.camera);
  };

  renderSphere = radius => {
    var geometry = new THREE.SphereGeometry(radius, 32, 32);

    var wireframe = new THREE.WireframeGeometry(geometry);
    var material = new THREE.LineBasicMaterial({
      color: 0xffffff,
      linewidth: 2
    });

    this.line = new THREE.LineSegments(wireframe, material);
    this.line.material.depthTest = true;
    this.line.material.opacity = 0.1;
    this.line.material.transparent = true;

    this.scene.add(this.line);
  };

  renderAxis = (label, value, radius, phi, theta) => {
    var material = new THREE.LineBasicMaterial({ color: 0x0000ff });
    var geometry = new THREE.Geometry();
    var v1 = new THREE.Vector3(0, 0, 0);
    var v2 = new THREE.Vector3();
    v2.setFromSphericalCoords(radius, phi, theta);
    geometry.vertices.push(v1);
    geometry.vertices.push(v2);
    var line = new THREE.Line(geometry, material);
    this.scene.add(line);

    var spritey = this.makeTextSprite(label, {
      fontsize: 50,
      borderColor: { r: 255, g: 0, b: 0, a: 1.0 },
      backgroundColor: { r: 255, g: 100, b: 100, a: 0.8 }
    });
    spritey.position.setFromSphericalCoords(radius, phi, theta);
    this.scene.add(spritey);

    var v3 = new THREE.Vector3();
    v3.setFromSphericalCoords(value, phi, theta);
    this.skillsValue.push(v3);

    var v4 = new THREE.Vector3();
    v4.setFromSphericalCoords(100, phi, theta);
    this.axisValue.push(v4);
  };

  renderPolygon = item => {
    this.renderFace(
      this.skillsValue[item.v1],
      this.skillsValue[item.v2],
      this.skillsValue[item.v3]
    );
  };

  renderBoundingWireframe = item => {
    this.renderFaceWireframe(
      this.axisValue[item.v1],
      this.axisValue[item.v2],
      this.axisValue[item.v3]
    );
  };

  renderFaceWireframe = (v1, v2, v3) => {
    var geom = new THREE.Geometry();

    geom.vertices.push(v1);
    geom.vertices.push(v2);
    geom.vertices.push(v3);

    geom.faces.push(new THREE.Face3(0, 1, 2));

    var wireframe = new THREE.WireframeGeometry(geom);
    var material = new THREE.LineBasicMaterial({
      color: 0xffffff,
      linewidth: 2
    });

    var line = new THREE.LineSegments(wireframe, material);
    line.material.depthTest = true;
    line.material.opacity = 0.3;
    line.material.transparent = true;

    this.scene.add(line);
  };

  renderFace = (v1, v2, v3) => {
    var geom = new THREE.Geometry();

    geom.vertices.push(v1);
    geom.vertices.push(v2);
    geom.vertices.push(v3);

    geom.faces.push(new THREE.Face3(0, 1, 2));
    geom.computeFaceNormals();

    var object = new THREE.Mesh(geom, new THREE.MeshNormalMaterial());

    this.scene.add(object);
  };

  makeTextSprite = (message, parameters) => {
    if (parameters === undefined) parameters = {};

    var fontface = parameters.hasOwnProperty("fontface")
      ? parameters["fontface"]
      : "Arial";

    var fontsize = parameters.hasOwnProperty("fontsize")
      ? parameters["fontsize"]
      : 18;

    var borderThickness = parameters.hasOwnProperty("borderThickness")
      ? parameters["borderThickness"]
      : 4;

    var borderColor = parameters.hasOwnProperty("borderColor")
      ? parameters["borderColor"]
      : { r: 0, g: 0, b: 0, a: 1.0 };

    var backgroundColor = parameters.hasOwnProperty("backgroundColor")
      ? parameters["backgroundColor"]
      : { r: 255, g: 255, b: 255, a: 1.0 };
    // var spriteAlignment = THREE.SpriteAlignment.topLeft;

    var canvas = document.createElement("canvas");
    var context = canvas.getContext("2d");
    context.font = "" + fontsize + "px " + fontface;

    // get size data (height depends only on font size)
    var metrics = context.measureText(message);
    var textWidth = metrics.width;

    // background color
    context.fillStyle =
      "rgba(" +
      backgroundColor.r +
      "," +
      backgroundColor.g +
      "," +
      backgroundColor.b +
      "," +
      backgroundColor.a +
      ")";
    // border color
    context.strokeStyle =
      "rgba(" +
      borderColor.r +
      "," +
      borderColor.g +
      "," +
      borderColor.b +
      "," +
      borderColor.a +
      ")";
    context.lineWidth = borderThickness;
    // roundRect(
    //   context,
    //   borderThickness / 2,
    //   borderThickness / 2,
    //   textWidth + borderThickness,
    //   fontsize * 1.4 + borderThickness,
    //   6
    // );
    // 1.4 is extra height factor for text below baseline: g,j,p,q.

    // text color
    context.fillStyle = "rgba(255, 255, 0, 1.0)";
    context.fillText(message, borderThickness, fontsize + borderThickness);

    // canvas contents will be used for a texture
    var texture = new THREE.Texture(canvas);
    texture.needsUpdate = true;
    var spriteMaterial = new THREE.SpriteMaterial({
      map: texture
      // useScreenCoordinates: false
      // alignment: spriteAlignment
    });
    var sprite = new THREE.Sprite(spriteMaterial);
    sprite.scale.set(50, 30, 1.0);
    return sprite;
  };

  bindEventListeners = () => {
    window.onresize = this.resizeCanvas;
  };

  resizeCanvas = () => {
    const width = this.mount.clientWidth;
    const height = this.mount.clientHeight;

    this.renderer.setSize(width, height);
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
  };

  render() {
    this.bindEventListeners();
    return (
      <div
        id="skills"
        ref={mount => {
          this.mount = mount;
        }}
      />
    );
  }
}

export default Skills;
