import React, { Component } from "react";
import NavBar from "./NavBar/NavBar";
import Content from "./Content/Content";
import "./MainContainer.css";

class MainContainer extends Component {
  state = {
    pages: [
      { name: "About", faIcon: "fas fa-question-circle" },
      { name: "Skills", faIcon: "fas fa-tools" },
      { name: "Works", faIcon: "fas fa-briefcase" },
      { name: "Contact", faIcon: "fas fa-at" }
    ],
    selectedPage: "About"
  };

  handleSelectContent = page => {
    if (this.state.selectedPage !== page) {
      setTimeout(() => {
        document.getElementById("content").style.transform =
          "translateX(-100%)";
      }, 0);
      setTimeout(() => {
        this.setState({ selectedPage: page });
        document.getElementById("content").style.transform = "translateX(0)";
      }, 500);
    }
  };

  render() {
    return (
      <div id="maincontainer">
        <NavBar
          pages={this.state.pages}
          selectedPage={this.state.selectedPage}
          onSelectContent={this.handleSelectContent}
        />
        <Content selectedPage={this.state.selectedPage} />
      </div>
    );
  }
}

export default MainContainer;
