import React, { Component } from "react";
import "./Works.css";
import Icon from "@material-ui/core/Icon";

class Works extends Component {
  state = {
    selectedWork: 0,
    works: [
      {
        name: "Arup 3D Portfolio",
        about:
          "An interactive 3D web scene featuring thirteen of Arup's hero projects in Sydney’s CBD. An automated workflow using FME was set up to easily add new 3D models of projects from various source formats (e.g. Revit, Rhino, Sketchup, etc.). The 3D data was then managed and published using the ArcGIS Platform. Built using React and Redux with the ArcGIS JavaScript API, the result is a slick, interactive web application showcasing Arup's impacts in the city of Sydney.",
        date: "Dec 2018 - Feb 2019",
        client: "Arup - Marketing & Communications",
        tags: [
          "JavaScript",
          "React",
          "Redux",
          "ArcGIS JavaScript API",
          "3D",
          "GIS",
          "FME"
        ],
        link: "https://digitalau.arup.com/arupportfolio",
        video: "/videos/arupportfolio3d.mp4"
      },
      {
        name: "Norfolk Island - Matterport Scans",
        about:
          "3D Matterport scans of the buildings in Norfolk Island were undertaken to assist in mapping out the existing fire safety measures within the buildings. This website was created to compile and present the results of the 3D scans in a single page interactive web viewer.",
        date: "Apr 2019",
        client: "Arup - Fire Engineering",
        tags: ["JavaScript", "React", "Redux", "ArcGIS JavaScript API", "3D"],
        link: "https://gis-aus.arup.com/norfolk-island/",
        video: "/videos/norfolkisland.mp4"
      },
      {
        name: "ArcGIS Services + Potree and Three.js",
        about:
          "Demands for visualisations of 3D data combined with design models have been increasing in our transport and highways projects to aid in the engineering design process. Point cloud scans, 3D modelling and BIM are also becoming common in our major engineering projects. This project looks at the integration of point cloud data and 3D design models using Potree and Three.js, with the data hosted on ArcGIS Server. With Three.js, we are able to bring in and render ArcGIS Map and Feature Services (point, polyline and polygon features) in our Potree scene, bringing together point cloud scans, GIS and BIM data in an interactive 3D web viewer.",
        date: "Jan 2018 - Feb 2018",
        client: "Arup",
        tags: ["JavaScript", "Potree", "Three.js", "GIS"],
        link: "N/A",
        video: "/videos/potree.mp4"
      },
      {
        name: "Kooragang Coal Terminal Arrival Roads",
        about:
          "The Kooragang Coal Terminal development proposed new earthworks over soft soils to facilitate extensions to the existing railway line at the eastern section of Kooragang Island in Newcastle. Renardi undertook a geotechnical peer review of the design prepared by GHD, associated with the Kooragang Coal Terminal. He was involved in reviewing the detailed design of the new railway embankment and proposed ground improvement of the underlying soft estuarine clay. As part of the review process, Renardi carried out Plaxis 2D modelling, and settlement and stability analysis. Renardi’s rigorous and detailed review has allowed for the reduction of ground improvement required, resulting in considerable cost savings to the client.",
        date: "Jan 2016 - Dec 2017",
        client: "Australian Rail Track Corporation",
        tags: [
          "Geotechnics",
          "Rail",
          "Detailed Design",
          "Slope Stability",
          "Retaining Wall",
          "Site Investigation",
          "Construction Supervision",
          "Plaxis"
        ],
        link: "N/A",
        video: "/videos/kooragang.mp4"
      }
    ]
  };

  handleClickNext = () => {
    if (this.state.selectedWork === this.state.works.length - 1) {
      this.setState({ selectedWork: 0 });
    } else {
      this.setState({ selectedWork: this.state.selectedWork + 1 });
    }
  };

  handleClickPrev = () => {
    if (this.state.selectedWork === 0) {
      this.setState({ selectedWork: this.state.works.length - 1 });
    } else {
      this.setState({ selectedWork: this.state.selectedWork - 1 });
    }
  };

  render() {
    return (
      <div id="works">
        <div id="stage">
          <video key={this.state.selectedWork} id="stage-video" loop autoPlay>
            <source
              src={
                process.env.PUBLIC_URL +
                this.state.works[this.state.selectedWork].video
              }
              type="video/mp4"
            />
            <source
              src={
                process.env.PUBLIC_URL +
                this.state.works[this.state.selectedWork].video
              }
              type="video/ogg"
            />
            Your browser does not support the video tag.
          </video>
        </div>
        <div id="panel">
          <div id="header">
            <div id="left-button" onClick={this.handleClickPrev}>
              <Icon>keyboard_arrow_left</Icon>
            </div>
            <div id="title">
              {this.state.works[this.state.selectedWork].name}
            </div>
            <div id="right-button" onClick={this.handleClickNext}>
              <Icon>keyboard_arrow_right</Icon>
            </div>
          </div>
          <div id="panel-content-container">
            <div className="panel-content">
              <div className="panel-content-title">About</div>
              {this.state.works[this.state.selectedWork].about}
            </div>
            <div className="panel-content">
              <div className="panel-content-title">Date</div>
              {this.state.works[this.state.selectedWork].date}
            </div>
            <div className="panel-content">
              <div className="panel-content-title">Client</div>
              {this.state.works[this.state.selectedWork].client}
            </div>
            <div className="panel-content">
              <div className="panel-content-title">Link</div>
              <a href={this.state.works[this.state.selectedWork].link}>
                {this.state.works[this.state.selectedWork].link}
              </a>
            </div>
            <div className="panel-content">
              <div className="panel-content-title">Tags</div>
              {this.state.works[this.state.selectedWork].tags.map(tag => (
                <div className="tag">{tag}</div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Works;
