import React, { Component } from "react";
import "./Icon.css";

class Icon extends Component {
  state = {};
  render() {
    return (
      <div
        className="icon"
        onClick={() => this.props.onSelectContent(this.props.page.name)}
      >
        <i
          className={
            this.props.selectedPage === this.props.page.name
              ? this.props.page.faIcon + " selected"
              : this.props.page.faIcon
          }
        />
        <div className="tag">{this.props.page.name}</div>
      </div>
    );
  }
}

export default Icon;
