import React, { Component } from "react";
import "./Contact.css";

class Contact extends Component {
  state = {};

  render() {
    return (
      <div id="contact">
        <div class="social-btns">
          <a class="social-btn facebook" href="#">
            <i class="fa fa-facebook" />
          </a>
          <a class="social-btn instagram" href="#">
            <i class="fa fa-instagram" />
          </a>
          <a class="social-btn linkedin" href="https://au.linkedin.com/in/renardi-tanuwidjaja-62304051">
            <i class="fa fa-linkedin" />
          </a>
          <a class="social-btn github" href="https://github.com/renarditanuwidjaja">
            <i class="fa fa-github" />
          </a>
        </div>
      </div>
    );
  }
}

export default Contact;
