import React, { Component } from "react";
import "./Cover.css";
import $ from "jquery";

import CoverPhoto from "./cover-photo.jpg";

class Cover extends Component {
  state = {};

  handleCloseCover = () => {
    $("#cover").fadeOut(800);
  };

  render() {
    return (
      <React.Fragment>
        <div id="loading-screen" />
        <div id="cover">
          <div id="cover-container">
            <div className="cover-photo">
              <img src={CoverPhoto} />
            </div>
            <div className="cover-title">RENARDI TANUWIDJAJA</div>
            <div className="cover-text">
              Hi, a little bit about myself, I started out my career as a Civil
              / Geotech Engineer. I've always had a passion for coding to
              increase efficiency in the way we work and be able to deliver
              projects in unique, interactive ways. I'm now on a path to grow my
              skills as a Digital Engineer in the Engineering and Construction
              world. Please hit "Continue..." to get to know a little more about
              myself and some of my works.
            </div>
          </div>
          <div id="button-container">
            <button
              type="button"
              id="bootstrap-overrides-btn"
              className="btn btn-outline-light"
              onClick={() => this.handleCloseCover()}
            >
              Continue...
            </button>
          </div>
        </div>
      </React.Fragment>
    );
  }

  componentDidMount() {
    $("#loading-screen").fadeOut(800);
  }
}

export default Cover;
