import React, { Component } from "react";
import "./About.css";

import {
  VerticalTimeline,
  VerticalTimelineElement
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import Icon from "@material-ui/core/Icon";

class About extends Component {
  state = {};
  render() {
    return (
      <div id="about">
        <VerticalTimeline>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="2016 - present"
            style={{ borderShadow: "3px solid blue" }}
            iconStyle={{
              background: "rgb(33, 150, 243)",
              color: "#fff"
            }}
            icon={<Icon>work</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              Digital Engineer
            </h3>
            <h5 className="vertical-timeline-element-subtitle">Arup</h5>
            <p>
              Applied digital solutions and innovations in engineering projects.
              Implemented data strategy and BIM/CAD/GIS delivery of major
              infrastructure, transport and resources projects. Managed
              geospatial data, undertook spatial analyses and automation of
              workflow processes. Developed bespoke tools and front-end web
              applications.
            </p>
            <p className="content-italic">
              JavaScript | Node.js | React | Redux | Three.js | Potree | Esri
              ArcGIS | FME | Python | SQL | AutoDesk | Bentley
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="2012 - 2016"
            iconStyle={{
              background: "rgb(33, 150, 243)",
              color: "#fff"
            }}
            icon={<Icon>work</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              Geotechnical Engineer
            </h3>
            <h5 className="vertical-timeline-element-subtitle">Arup</h5>
            <p>
              Carried out and managed geotechnical site investigation, including
              borehole drilling, test pit excavation, geophysics and overwater
              investigations. Produced factual and interpretive reports.
              Undertook concept and detailed design of highways, rail, bridge
              and maritime projects. Undertook geotechnical analysis and
              numerical modelling (2D and 3D). Management of geotechnical
              datasets. Digital innovation and automation of workflow processes.
            </p>
            <p className="content-italic">
              Site Investigation | Construction Supervision | Slope Analysis |
              Retaining Wall Design | Foundation Design | Desk Study | Numerical
              Modelling | Plaxis | gINT | Oasys
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="2011 - 2012"
            iconStyle={{
              background: "rgb(33, 150, 243)",
              color: "#fff"
            }}
            icon={<Icon>work</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              Junior Geotechnical Engineer
            </h3>
            <h5 className="vertical-timeline-element-subtitle">URS</h5>
            <p>
              Carried out borehole and test pit logging. Contributed and
              authored geotechnical factual and interpretive reports. Generated
              borehole and test pit logs, charts and geological sections.
            </p>
            <p className="content-italic">
              Site Investigation | Instrumentation and Monitoring | Geotechnical
              Reporting | Desk Study | gINT
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--work"
            date="2011"
            iconStyle={{
              background: "rgb(33, 150, 243)",
              color: "#fff"
            }}
            icon={<Icon>work</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              Junior Project Engineer
            </h3>
            <h5 className="vertical-timeline-element-subtitle">
              Waverley Council
            </h5>
            <p>
              Managed local civil infrastructure projects, including roads and
              public facilities. Engaged subcontractors and coordinated works.
            </p>
            <p className="content-italic">
              Project Management | Contract Administration | Site Supervision |
              Reporting
            </p>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--education"
            date="2007 - 2012"
            iconStyle={{
              background: "rgb(245, 0, 87)",
              color: "#fff"
            }}
            icon={<Icon>school</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              Bachelor of Engineering (Civil) and Commerce (Finance) - 1st Class
              Honours
            </h3>
            <h5 className="vertical-timeline-element-subtitle">
              University of New South Wales
            </h5>
          </VerticalTimelineElement>
          <VerticalTimelineElement
            className="vertical-timeline-element--education"
            date="2001 - 2006"
            iconStyle={{
              background: "rgb(245, 0, 87)",
              color: "#fff"
            }}
            icon={<Icon>school</Icon>}
          >
            <h3 className="vertical-timeline-element-title">
              High School Certificate
            </h3>
            <h5 className="vertical-timeline-element-subtitle">
              Marcellin College Randwick
            </h5>
          </VerticalTimelineElement>
        </VerticalTimeline>
      </div>
    );
  }
}

export default About;
